import React, { useState, useEffect, useLayoutEffect } from 'react';
import { Parallax, useParallaxController } from 'react-scroll-parallax';

import './Technology.scss';

import arrowImg from '../../assets/Technology/arrowDown.svg';
import sensorImg from '../../assets/Technology/sensor.svg';
import image1Img from '../../assets/Technology/image1.jpg';

import bannerImg from '../../assets/Technology/banner.svg';
import globeImg from '../../assets/Technology/globe.png';
import ImageCard from './ImageCard/ImageCard';

const Technology: React.FC = () => {
    const parallaxController = useParallaxController();

    const handleLoad = () => parallaxController?.update();

    const screenHeight = window.innerHeight;

    const listenToScroll = () => {
        const winScroll = document.body.scrollTop || document.documentElement.scrollTop;

        // TODO: Get Screen height (dynamic), also do onResize;
        const scrolled = winScroll / window.innerHeight;

        const intro = document.getElementById('intro')!;
        const background = document.getElementById('background')!;
        const exploreMore = document.getElementById('exploreMore')!;

        if (scrolled < 1) {
            // Full Screen Positions
            intro.style.position = 'fixed';
            background.style.position = 'fixed';

            // Circle Size
            background.style.webkitMaskSize = `${scrolled * 150}%`;
            exploreMore.style.webkitMaskSize = '0%';
        } else if (scrolled >= 1 && scrolled < 2) {
            // Full Screen Positions
            background.style.position = 'fixed';
            exploreMore.style.position = 'fixed';

            // Circle Size
            background.style.webkitMaskSize = '150%';
            exploreMore.style.webkitMaskSize = `${(scrolled - 1) * 150}%`;

            // Explore More Position
            exploreMore.style.top = '0';
        } else {
            // Full Screen Positions
            intro.style.position = 'absolute';
            background.style.position = 'absolute';
            exploreMore.style.position = 'absolute';

            // Explore More Position
            exploreMore.style.top = '200vh';
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', listenToScroll);
        return (() => {
            window.removeEventListener('scroll', listenToScroll);
        });
    });

    useLayoutEffect(() => {
        const handler = () => parallaxController?.update();
        window.addEventListener('load', handler);
        return () => window.removeEventListener('load', handler);
    }, [parallaxController]);

    return (
        <div className="technologyContainer">

            <header className="intro" id="intro">
                <h1>
                    World&#8217;s most experienced
                    <br />
                    airside driver.
                </h1>
            </header>

            <section className="background" id="background">
                <h1>
                    World&#8217;s most experienced
                    <br />
                    airside driver.
                </h1>
            </section>

            <section className="exploreMore" id="exploreMore">
                <h1>Explore More</h1>
                <img src={arrowImg} alt="down arrow" />
            </section>

            <section className="fullScreen" />
            <section className="fullScreen" />
            <section className="fullScreen" />

            <section className="statsContainer">
                <Parallax
                    translateY={['-300px', '0px']}
                    opacity={[0, 1]}
                    className="sensorContainer"
                    startScroll={screenHeight * 2.5}
                    endScroll={screenHeight * 3}
                >
                    <img src={sensorImg} alt="sensors" onLoad={handleLoad} />
                </Parallax>
                <div className="lineContainer">
                    <div className="line" />
                    <div className="line" />
                </div>
                <Parallax
                    translateY={['300px', '0px']}
                    opacity={[0, 1]}
                    startScroll={screenHeight * 2.5}
                    endScroll={screenHeight * 3}
                >
                    <div className="dataContainer">
                        <div className="dataPoint">
                            <h3>360</h3>
                            <p>Degree Awareness</p>
                        </div>
                        <div className="dataPoint">
                            <h3>10</h3>
                            <p>Robust Sensors</p>
                        </div>
                        <div className="dataPoint">
                            <h3>1.2M</h3>
                            <p>LiDAR Points Per Second</p>
                        </div>
                        <div className="dataPoint">
                            <h3>5</h3>
                            <p>Redundant Systems</p>
                        </div>
                    </div>
                </Parallax>
            </section>

            <section className="imageCards">
                <div className="imageRow">
                    <Parallax
                        translateX={['-100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 3.5}
                        endScroll={screenHeight * 4}
                        className="parallaxImage"
                    >
                        <ImageCard
                            src={image1Img}
                            title="Card Title 1"
                            content="Lorem ipsum dolor sit amet,consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
                        />
                    </Parallax>
                    <Parallax
                        translateY={['-100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 3.5}
                        endScroll={screenHeight * 4}
                        className="parallaxImage"
                    >
                        <ImageCard
                            src={image1Img}
                            title="Card Title 2"
                            content="Lorem ipsum dolor sit amet,consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
                        />
                    </Parallax>
                </div>
                <div className="imageRow">
                    <Parallax
                        translateY={['100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 3.5}
                        endScroll={screenHeight * 4}
                        className="parallaxImage"
                    >
                        <ImageCard
                            src={image1Img}
                            title="Card Title 3"
                            content="Lorem ipsum dolor sit amet,consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
                        />
                    </Parallax>
                    <Parallax
                        translateX={['100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 3.5}
                        endScroll={screenHeight * 4}
                        className="parallaxImage"
                    >
                        <ImageCard
                            src={image1Img}
                            title="Card Title 4"
                            content="Lorem ipsum dolor sit amet,consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
                        />
                    </Parallax>
                </div>
            </section>

            <section className="achievement">
                <header>
                    <Parallax
                        translateY={['100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 4.5}
                        endScroll={screenHeight * 5}
                    >
                        <h2>
                            {'Oh. So much '}
                            <span>data.</span>
                        </h2>
                    </Parallax>
                    <Parallax
                        translateY={['100px', '0px']}
                        opacity={[0, 1]}
                        startScroll={screenHeight * 4.75}
                        endScroll={screenHeight * 5}
                    >
                        <h3>Lots of high-quality data = smarter driver</h3>
                    </Parallax>
                </header>
                <Parallax
                    translateX={['100px', '0px']}
                    opacity={[0, 1]}
                    startScroll={screenHeight * 5}
                    endScroll={screenHeight * 5.25}
                >
                    <div className="banner">
                        <Parallax
                            translateX={['100px', '0px']}
                            opacity={[0, 1]}
                            startScroll={screenHeight * 5}
                            endScroll={screenHeight * 5.25}
                        >
                            <img src={bannerImg} alt="banner" />
                        </Parallax>
                        <img className="globe" src={globeImg} alt="globe" />
                        <p>
                            We built the world&rsquo;s
                            {' '}
                            <b>largest proprietary dataset of airside driving data,</b>
                            <br />
                            using the AeroVect Explorer, our custom-built mapping kit that lets us build a
                            <br />
                            digital twin of a major airport in
                            {' '}
                            <b>less than 2 hours</b>
                            .
                        </p>
                    </div>
                </Parallax>
                <h3>Oh, and we have already mapped half of America&apos;s top 10 airports.</h3>
            </section>
        </div>
    );
};

export default Technology;
